.myBtn {
    padding: 5px 15px;

    font-family: 'Open Sans', sans-serif;
    font-size: 14px;

    background-color: #fff;
    border: 2px solid darkblue;
    color: darkblue;
    cursor: pointer;

    transition: background-color .2s linear, color .2s linear;
}

.myBtn:hover {
    background-color: darkblue;
    color: #fff;
}