body {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

p {
    margin: 0;
}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App {
    width: 800px;
}

.posts {
    padding: 0 300px;

    background: url("../bg.jpg") center no-repeat;
    width: 100%;
    height: 100%;
    background-size: cover;
}

/* Post */

.post {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
    padding: 15px;

    background-color: #fff;
    border: 2px solid darkblue;
}

/* react-transition-group */

.remove-btn {
    margin-right: 0.5rem;
}

/* Начало анимации добавления поста */

.post-enter {
    transform: translateX(-100%);
    transition: transform 500ms linear;
}

/* Конец анимации добавления поста */

.post-enter-active {
    transform: translateX(0);
    transition: transform 500ms linear;
}

/* Начало анимации удаления поста */

.post-exit {
    opacity: 1;
}

/* Конец анимации удаления поста */

.post-exit-active {
    transform: translateX(-100%);
    transition: transform 500ms linear;
}

/* Pagination */

.pages {
    display: flex;
    margin: 20px 0;
}

.pages__item {
    margin-right: 10px;
    padding: 10px;

    background-color: #fff;
    border: 2px solid darkblue;
    color: darkblue;
    cursor: pointer;

    transition: background-color .2s linear, color .2s linear;
}

.pages__item:hover {
    background-color: darkblue;
    color: #fff;
}

.pages__item:last-child {
    margin-right: 0;
}

.pages__item.active {
    border: 2px solid red;
}

/* Nav */

.nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: 50px;
    padding: 0 15px;

    background-color: #fff;
    box-shadow: 0 2px 5px #000;
}

.nav__item {
    margin-right: 15px;
    padding: 5px 15px;

    font-family: 'Open Sans', sans-serif;
    font-size: 14px;

    background-color: #fff;
    border: 2px solid darkblue;
    color: darkblue;
    cursor: pointer;
    text-decoration: none;

    transition: background-color .2s linear, color .2s linear;
}

.nav__item:hover {
    background-color: darkblue;
    color: #fff;
}

.nav__item:last-child {
    margin-right: 0;
}