.loader {
    width: 100px;
    height: 100px;
    margin: 100px auto;

    font-size: 0;
    color: transparent;

    border: 3px dashed #ccc;
    border-radius: 50%;

    animation: rotate 1s linear infinite;
}

@keyframes rotate {
    from {
        transform: rotate(0);
    }
    to {
        transform: rotate(360deg);
    }
}