.deleteBtn {
    padding: 5px 15px;

    font-family: 'Open Sans', sans-serif;
    font-style: 14px;

    background-color: #fff;
    border: 2px solid red;
    color: red;
    cursor: pointer;

    transition: background-color .2s linear, color .2s linear;
}

.deleteBtn:hover {
    background-color: red;
    color: #fff;
}