.myModal {
    display: none;

    background-color: rgba(0, 0, 0, .5);

    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}

.myModalContent {
    min-width: 250px;
    padding: 25px;

    background-color: #fff;
    border-radius: 15px;
}